<template>
  <div>
<!--form表单-->
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item label="时间选择">
      <div class="block">
<!--        <span class="demonstration">时间选择</span>-->
        <el-date-picker
            v-model="formInline.times"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            align="right">
        </el-date-picker>
      </div>
      </el-form-item>

      <el-form-item label="机构ID">
        <el-select v-model="formInline.fixmedins_code" placeholder="活动区域" filterable>
          <el-option v-for="it in jgid" :label="it.name" :value="it.code"></el-option>

        </el-select>
      </el-form-item>

      <el-form-item label="病人姓名">
        <el-input v-model="formInline.param" placeholder="支持模糊查询"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit">查询</el-button>
      </el-form-item>
    </el-form>

  <!--  表格区域-->
    <el-table
        :data="tableData"
        border
        style="width: 100%"
        height="750">
      <el-table-column
          prop="psn_name"
          label="病人姓名"
          align="center"
          width="220">

      </el-table-column>
      <el-table-column
          prop="psn_no"
          align="center"
          label="人员编号">

      </el-table-column>
      <el-table-column
          prop="mdtrt_id"
          align="center"
          label="就诊id">
      </el-table-column>

      <el-table-column
          label="操作"
          align="center">
        <template scope="scope">

          <el-button type="primary" @click="alterMsg(scope.row.result)"> 生成在院信息冲销json</el-button>
          <el-button type="success" @click="alterMsg(scope.row.result_cyzt)"> 生成出院状态冲销json</el-button>
        </template>
      </el-table-column>

    </el-table>


    <el-dialog
        title="冲销json"
        :visible.sync="dialogVisible"
        width="30%">
      <span>{{msg}}</span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="copy">复制</el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
import {jgid} from '../../CommonUtils/localconstant'

export default {
  name: "YbDjcx",
  data() {
    return {
      jgid:jgid,
      dialogVisible:false,
      msg:"",
      /*table数据*/
      tableData: [],

      /*form数据*/
      formInline: {
        param: '',
        fixmedins_code: '',
        times: []
      }
    }
  },
  created() {
    let b=new Date();
    b.setTime(b.getTime()-1000*60*60*24*30);
    this.formInline.times=[b,new Date()]
  },

  methods: {

    getData: function () {
      let ls_data=Object.assign({},this.formInline);
      ls_data.starttime=ls_data.times[0].format('yyyy-MM-dd');
      ls_data.endtime=ls_data.times[1].format('yyyy-MM-dd');
      delete ls_data.times
      console.log(JSON.stringify(ls_data));
      this.$http.post("/yb/zyxx",JSON.stringify(ls_data)).then(res=>{
        if(res.data.code=='200'){
          this.tableData=res.data.data
        } else this.$message.error('接口访问失败'+res.data.data)
      })
    },

    onSubmit() {
      console.log('submit!');
      this.getData();
    },

    alterMsg(msg){
      this.msg=msg;
      this.dialogVisible=true;
    },
    copy(){
      let inp=document.createElement("textarea");
      document.body.append(inp);

      inp.value=this.msg;
      inp.select();

      document.execCommand('copy');
      inp.remove();
      this.$message({message:'复制成功',type:"success"})
    }
  }
}


</script>


