<template>
<div>
  <!--表单检索内容-->
  <el-form :model="queryParams" ref="queryForm" :inline="true" label-width="68px">
    <el-form-item label="登录地址" prop="ipaddr">
      <el-input
          v-model="queryParams.ipaddr"
          placeholder="请输入登录地址"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
      />
    </el-form-item>
    <el-form-item label="用户名称" prop="userName">
      <el-input
          v-model="queryParams.userName"
          placeholder="请输入用户名称"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
      />
    </el-form-item>
    <el-form-item>
      <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
      <el-button type="primary" plain icon="el-icon-refresh" size="mini" @click="resetQuery">刷新</el-button>
    </el-form-item>
  </el-form>

<!--表格内容-->
  <el-table
      :data="tableData"
      stripe
      style="width: 100%">

    <el-table-column
        prop="token"
        label="会话编号"
        align="center"
        width="220">
    </el-table-column>

    <el-table-column
        prop="ryxm"
        label="人员姓名"
        align="center"
        width="160">
    </el-table-column>

    <el-table-column
        prop="rybm"
        label="人员编码"
        align="center"
        width="160">
    </el-table-column>

    <el-table-column
        prop="logintime"
        label="登录时间"
        align="center"
        width="220">
    </el-table-column>

    <el-table-column
        prop="ip"
        label="ip地址"
        align="center"
        width="220">
    </el-table-column>

    <el-table-column
        prop="browserName"
        label="浏览器"
        align="center"
        width="180">
    </el-table-column>

    <el-table-column
        prop="osName"
        label="操作系统"
        align="center"
        width="180">
    </el-table-column>

    <el-table-column
        prop="lsdate"
        label="备注"
        align="center">
<!--      <template slot-scope="scope">
        <div>{{this.lssession}}</div>
      </template>-->
    </el-table-column>

    <el-table-column
        prop=""
        label="操作"
        align="center"
        width="120">
      <template slot-scope="scope">
        <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleForceLogout(scope.row.logintoken)"
        >强退</el-button>
      </template>
    </el-table-column>

  </el-table>
</div>
</template>

<script>
export default {
name: "zxyh",
  data(){
  return{
    queryParams: {
      ipaddr: undefined,
      userName: undefined
    },

    /*分页查询信息*/
    queryInfo: {
      query: '', //作为搜索条件
      pagenum: 1,  //当前页数
      pagesize: 10 //当前每页显示数量
    },

    tableData:[],
  }
  },
  created() {
    this.getList();
  },
  methods:{
 /**获取登录用户信息*/
    getList:function (){
    this.$http.get("/xtjk/zxyh").then(res=>{
      this.tableData=res.data.data

      //判断是否本机（功能实现了但效果没写好）
      for(var i=0;i<this.tableData.length;i++){
        this.$set(this.tableData[i],"lsdate",this.tableData[i].logintoken==window.sessionStorage.getItem('token')?'本机登录':'其他登录')
      }
      console.log(this.tableData)
    })
    },

    /** 搜索按钮操作 */
    handleQuery() {
      this.pageNum = 1;
      this.getList();
    },

    /** 刷新按钮操作 */
    resetQuery() {
/*      this.resetForm("queryForm");
      this.handleQuery();*/
      this.getList()
    },

    /*强制退出*/
    handleForceLogout:function (token){
      this.$http.get("/xtjk/handleForceLogout?token="+token).then(res=>{
        if(res.data.code==200){
          this.$message.success("强退成功")
          this.getList()
        }else this.$message.error("强退失败")
      })
    }
  }
}
</script>

<style scoped>

</style>
