<template>
<div>
  <!--form表单-->
  <el-form :inline="true" :model="formInline" class="demo-form-inline">

<!--    <el-form-item label="cookie" label-width="80px" size="medium">
      <el-input v-model="formInline.cookie" placeholder="请输入cookoe" style="width:900px;"></el-input>
    </el-form-item>


    <el-form-item label="token" label-width="60px" size="medium">
      <el-input v-model="formInline.token" placeholder="请输入token" style="width:500px;"></el-input>
    </el-form-item>-->

    <el-form-item label="身份证号码" label-width="100px" size="medium" >
      <el-input v-model="formInline.sfzh" placeholder="身份证号码" style="width:400px;"></el-input>
    </el-form-item>

    <el-form-item label="类型" label-width="100px" size="medium">
      <el-select v-model="formInline.insutype" placeholder="请选择活动区域">
        <el-option label="职工" value="310"></el-option>
        <el-option label="居民" value="390"></el-option>
      </el-select>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" @click="onSubmit">查询</el-button>
    </el-form-item>
  </el-form>

<!--  表格区域-->
  <el-table
      :data="tableData"
      border
      style="width: 100%"
      height="750">
    <el-table-column
        prop="psn_name"
        label="病人姓名"
        align="center"
        width="100">

    </el-table-column>
    <el-table-column
        prop="psn_no"
        align="center"
        label="人员编号"
        width="150">
    </el-table-column>

    <el-table-column
        prop="lx"
        align="center"
        label="类型"
        width="100">
    </el-table-column>

    <el-table-column
        prop="fixmedins_name"
        align="center"
        label="医院">
    </el-table-column>
    <el-table-column
        prop="mdtrt_id"
        align="center"
        label="就诊id">
    </el-table-column>

    <el-table-column
        prop="setl_id"
        align="center"
        label="结算id">
    </el-table-column>

    <el-table-column
        prop="setlTime"
        align="center"
        label="结算时间">
    </el-table-column>

    <el-table-column
        prop="ylzfy"
        align="center"
        label="总金额"
        width="100">
    </el-table-column>
    <el-table-column
        label="操作"
        align="center">
      <template scope="scope">

        <el-button type="primary" @click="alterMsg(scope.row.ls_sc)"> 生成结算信息冲销json</el-button>
      </template>
    </el-table-column>

  </el-table>


  <el-dialog
      title="冲销json"
      :visible.sync="dialogVisible"
      width="30%">
    <span>{{msg}}</span>
    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="copy">复制</el-button>
  </span>
  </el-dialog>

</div>
</template>

<script>


export default {
name: "YbJscx",
  data() {
    return {
      dialogVisible:false,
      msg:"",
      /*table数据*/
      tableData: [],

      /*form数据*/
      formInline: {
        insutype: '390',
        sfzh: '500236199305152977',
        token:'be1a88fd-f0ef-4149-aac8-bdbce29875c9',
        cookie:'XSRF-TOKEN=be1a88fd-f0ef-4149-aac8-bdbce29875c9; SESSION=Mzc2ZWQzMGQtODlmZS00OTcyLWJkMzUtZjk5MjAwYjMzNDcw'
      }
    }
  },
  methods:{
    getData: function () {

      console.log(JSON.stringify(this.formInline));
      this.$http.post("/yb/drmzzycx",JSON.stringify(this.formInline)).then(res=>{
        if(res.data.code=='200'){
        this.tableData=res.data.data
        } else this.$message.error('接口访问失败'+res.data.data)
      })
    },
    onSubmit() {
      this.getData();
    },
    alterMsg(msg){
      this.msg=msg;
      this.dialogVisible=true;
    },
    copy(){
      let inp=document.createElement("textarea");
      document.body.append(inp);

      inp.value=this.msg;
      inp.select();

      document.execCommand('copy');
      inp.remove();
      this.$message({message:'复制成功',type:"success"})
    }
  }

}
</script>

<style scoped>

</style>
