import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
    //存状态，取出方式：$store.state.XXXX
    state:{
        counter:100
    },

    //定义改变state数据的方法（同步操作，异步用actions,在mutations里面处理），外部调用：this.$store.commit('方法名',参数)，参数为playload
    mutations:{
        //定义改变state数据的具体方法
        //默认参数为state
        mutationsExamples(state){

        },

        //通过actions改变state里面的值的具体方法，外部调用：this.$store.dispatch(‘action方法名’,值)
        mutationsExamples1(state){

        }
    },

    //接受异步数据在，在mutations里面处理
    actions:{
        //默认参数是context
        actionsExamples(context){
            //通过context.commit调用mutations里面的方法，同时在mutat
            context.commit('mutationsExamples1')
        }

    },
    //vuex中的计算属性，取出方式：$store.getter.XXXXX
    getters:{
        GettersExamples(state){
            return state.counter * state.counter
        }
    }




})


export default store
